"use client";

import Link from "next/link";
import Image from "next/image";
import React, { useEffect, useState } from "react";
import { auth } from "@/app/FirebaseDB/firebase.config";
import { useRouter } from "next/navigation";

const Navbar: React.FC = () => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isDropdownOpen, setDropdownOpen] = useState(false);

  const toggleDropdown = () => setDropdownOpen(!isDropdownOpen);
  const closeDropdown = () => setDropdownOpen(false);

  const router = useRouter();

  useEffect(() => 
  {
    // Check if a user is logged in
    const user = auth.currentUser;
    setIsLoggedIn(user ? true : false); // If user exists, they're logged in
  }, []); // Empty dependency array ensures this runs only once, when the component is mounted

  // Check if the user is logged in using Firebase auth
  useEffect(() => 
  {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      setIsLoggedIn(user ? true : false); // If user is logged in, set isLoggedIn to true
    });

    return () => unsubscribe(); // Cleanup on unmount
  }, []);

  // Function to log out the user
  const handleLogout = async () => {
    try {
      await auth.signOut(); // Sign out the user
      setIsLoggedIn(false); // Update the state
      router.push("/login"); // Redirect to login page (optional)
      console.log("Signed out");
    } catch (error) {
      console.error("Error signing out: ", error); // Handle errors
    }
  };


  return (
    <nav className="bg-[#457B9D] text-white px-6 py-4">
      <div className="container mx-auto flex justify-between items-center">

        {/* Logo */}
        <span className="text-2xl font-bold">
          <Link href="#">
            <img className="max-w-14" src="./geoquest_logo.png" alt="GeoQuest"/>
          </Link>
        </span>

        {/* Navigation Links */}
        <div className="flex space-x-36">
          <Link href="/game" className="text-xl font-bold hover:underline"> –ò–≥—Ä–∞–π </Link>
          <Link href="/leaderboard" className="text-xl font-bold hover:underline"> –ö–ª–∞—Å–∞—Ü–∏—è </Link>
          <Link href="/about-us" className="text-xl font-bold hover:underline"> –ó–∞ –Ω–∞—Å </Link>
        </div>

        {/* Profile Dropdown */}
        <div className="relative">
          <button
            onClick={toggleDropdown}
            className="flex items-center focus:outline-none transition duration-300"
          >

          <Image
            src={isLoggedIn ? "/user_icon.png" : "/navbar_unlogged_menu.png"}
            alt="Account button"
            width={56}
            height={56}
            className="max-w-10"/>


{/*
            {
              isLoggedIn ? <Image
                              src="/user_icon.png"
                              alt="Account button"
                              width={56}
                              height={56}
                              className="max-w-10"/>
                          : 
                          <Image
                            src="/navbar_unlogged_icon.png"
                            alt="Sign in/Log in button"
                            width={56}
                            height={56}
                            className="max-w-10"/>
            }
            */}
          </button>

          {isDropdownOpen && (
            <div
              className="absolute right-0 mt-2 w-48 bg-[#FFF9F5] text-[#1D3557] rounded-lg shadow-lg overflow-hidden z-10"
              onMouseLeave={closeDropdown}
            >

            {isLoggedIn ? 
              (<button
                  onClick={handleLogout}
                  className="block w-full text-left px-4 py-3 text-lg font-semibold hover:bg-[#A8DADC] hover:text-[#457B9D] transition duration-300"
                >
                  –ò–∑—Ö–æ–¥
                </button>) 
              : 
              (<div>
                  <Link
                    href="/login"
                    className="block px-4 py-3 text-lg font-semibold hover:bg-[#A8DADC] hover:text-[#457B9D] transition duration-300"
                  >
                    –í—Ö–æ–¥
                  </Link>

                  <Link
                    href="/register"
                    className="block px-4 py-3 text-lg font-semibold hover:bg-[#A8DADC] hover:text-[#457B9D] transition duration-300"
                  >
                    –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è
                  </Link>
                </div>
              )}


              {/*
            {
              isLoggedIn ? <div
                              className="absolute right-0 mt-2 w-48 bg-[#FFF9F5] text-[#1D3557] rounded-lg shadow-lg overflow-hidden z-10"
                              onMouseLeave={closeDropdown}
                            >
                              <button
                                onClick={handleLogout}
                                className="block w-full text-left px-4 py-3 text-lg font-semibold hover:bg-[#A8DADC] hover:text-[#457B9D] transition duration-300"
                              >
                                –ò–∑—Ö–æ–¥
                              </button>
                            </div> 
                            : 
                            <div>
                              <Link
                                href="/login"
                                className="block px-4 py-3 text-lg font-semibold hover:bg-[#A8DADC] hover:text-[#457B9D] transition duration-300"
                              >
                                –í—Ö–æ–¥
                              </Link>

                              <Link
                                href="/register"
                                className="block px-4 py-3 text-lg font-semibold hover:bg-[#A8DADC] hover:text-[#457B9D] transition duration-300"
                              >
                                –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è
                              </Link>
                            </div>
            }
          */}
            </div>
          )}
        </div>
      </div>
    </nav>
  );
};

export default Navbar;

